import Card from "@mui/material/Card";
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDInput from "components/MDInput";
import MDButton from "components/MDButton";
import { toast } from "react-toastify";
import BasicLayout from "layouts/authentication/components/BasicLayout";
import { axiosInstance } from "../../../apiInstances/index";
import { useState } from "react";

import { useNavigate } from "react-router-dom";
import "./index.css";

function Basic() {
  const [adminData, setadminData] = useState([{ userEmail: "", UserPassword: "" }]);
  let navigate = useNavigate();

  const userLogin = (e) => {
    e.preventDefault();

    try {
      const user = {
        email: "admin@gmail.com",
        username: "admin@123",
        password: "Admin@123",
      };
      if (adminData.userEmail === user.email || adminData.userEmail === user.username) {
        if (adminData.UserPassword === user.password) {
          toast.success("Login success");
          navigate("/rafffel");
        } else {
          toast.error("wrong password");
        }
      } else {
        toast.info("check username or Email");
      }
    } catch (error) {
      console.error(error);
    }
  };

  const userInput = (e) => {
    const { name, value } = e.target;

    setadminData({
      ...adminData,
      [name]: value,
    });
  };

  return (
    <>
      <BasicLayout>
        <Card>
          <MDBox
            variant="gradient"
            bgColor="info"
            borderRadius="lg"
            coloredShadow="info"
            mx={2}
            mt={-3}
            p={2}
            mb={1}
            textAlign="center"
            className="TitleColor"
          >
            <MDTypography variant="h4" fontWeight="medium" color="white" mt={1}>
              Sign In
            </MDTypography>
          </MDBox>
          <MDBox pt={4} pb={3} px={3}>
            <MDBox component="form" role="form" onSubmit={userLogin}>
              <MDBox mb={2}>
                <MDInput
                  type="email"
                  name="userEmail"
                  label="Email"
                  fullWidth
                  onChange={userInput}
                  required
                />
              </MDBox>
              <MDBox mb={2}>
                <MDInput
                  type="password"
                  name="UserPassword"
                  label="Password"
                  fullWidth
                  onChange={userInput}
                />
              </MDBox>
              <MDBox mt={4} mb={1}>
                <MDButton
                  variant="gradient"
                  color="info"
                  fullWidth
                  type="submit"
                  className="TitleColor"
                >
                  sign in
                </MDButton>
              </MDBox>
            </MDBox>
          </MDBox>
        </Card>
      </BasicLayout>
    </>
  );
}

export default Basic;
