import { useNavigate } from "react-router-dom";
import { useEffect, useState } from "react";

import "./raffle.css";
import axios from "axios";

function Raffle() {
  let navigate = useNavigate();

  const [walletAddress, setWalletAddress] = useState(null);

  const [isSolanaNftData, setIsSolanaNftData] = useState([]);

  const [loading, setLoading] = useState(true);

  // Detect Phantom Wallet
  window.onload = async () => {
    try {
      if (window?.solana) {
        const solana = window?.solana;
        if (solana?.isPhantom) {
          console.info(`Phantom wallet found`);

          const res = await solana?.connect();
          localStorage.setItem("adminWallet", res.publicKey.toString());

          setWalletAddress(res?.publicKey?.toString());
          try {
            const config = {
              headers: {
                "Content-Type": "application/json",
              },
            };
            const data = {
              jsonrpc: "2.0",
              method: "qn_fetchNFTs",
              params: {
                wallet: res?.publicKey?.toString(),
                omitFields: ["provenance", "traits"],
              },
            };
            axios
              .post(
                "https://small-chaotic-paper.solana-mainnet.discover.quiknode.pro/e7883b6278111e0164f776d1c4edcbfb7136dd03/",
                data,
                config
              )
              .then((response) => {
                // handle success
                console.log(response?.data?.result?.assets, "solana");
                setIsSolanaNftData(response?.data?.result?.assets);
                setLoading(false);
              })
              .catch((err) => {
                // handle error
                console.error(err);
              });
          } catch (error) {
            // handle error
            console.error(error);
          }
        } else {
          window.open("https://phantom.app/download");
        }
      }
    } catch (err) {
      console.log(err, "err");
    }
  };

  // Connect Phantom Wallet
  const connectWallet = async () => {
    if (window?.solana) {
      const solana = window?.solana;
      const res = await solana?.connect();
      setWalletAddress(res?.publicKey?.toString());
      localStorage.setItem("adminWallet", res?.publicKey?.toString());
      try {
        const config = {
          headers: {
            "Content-Type": "application/json",
          },
        };
        const data = {
          jsonrpc: "2.0",
          method: "qn_fetchNFTs",
          params: {
            wallet: res?.publicKey?.toString(),
            omitFields: ["provenance", "traits"],
          },
        };
        axios
          .post(
            "https://small-chaotic-paper.solana-mainnet.discover.quiknode.pro/e7883b6278111e0164f776d1c4edcbfb7136dd03/",
            data,
            config
          )
          .then((response) => {
            // handle success
            console.log(response?.data?.result?.assets, "solana");
            setIsSolanaNftData(response?.data?.result?.assets);
            setLoading(false);
          })
          .catch((err) => {
            // handle error
            console.error(err);
          });
      } catch (error) {
        // handle error
        console.error(error);
      }
    } else {
      // alert("Phantom Wallet not found");
      window.open("https://phantom.app/");
    }
  };

  const sendDeatils = (data) => {
    localStorage.setItem("nftDetails", JSON.stringify(data));
    navigate("/create-raffle");
  };

  const viewRaffle = () => {
    navigate("/view-raffle");
  };
  useEffect(() => connectWallet, []);

  // Devnet
  // https://magical-neat-sound.solana-devnet.discover.quiknode.pro/2973528e9f79d9e372a733ec87d9176ad475b402/

  // Mainnet
  // https://small-chaotic-paper.solana-mainnet.discover.quiknode.pro/e7883b6278111e0164f776d1c4edcbfb7136dd03/

  return (
    <>
      <div className="wallet-adapter-dropdown ">
        <button
          className="wallet-adapter-button bg-gradient-to-t from-indigo-600 to-indigo-500 border border-indigo-500  opacity-90 hover:opacity-100 px-5 mr-3 rounded-xl text-white font-bold transition"
          tabIndex={0}
          type="button"
          onClick={connectWallet}
          style={{ justifyContent: "space-between", pointerEvents: "auto" }}
        >
          <i className="wallet-adapter-button-start-icon">
            <img
              src="data:image/svg+xml;base64,"
              alt="Phantom icon"
            />
          </i>
          {!walletAddress ? (
            <p>Connect Wallet</p>
          ) : (
            <p>
              {walletAddress.slice(0, 4)}...{walletAddress.slice(-4)}
            </p>
          )}
        </button>
      </div>

      <div className="wallet-adapter-dropdown ">
        <button
          onClick={viewRaffle}
          className="wallet-adapter-button bg-gradient-to-t from-indigo-600 to-indigo-500 border border-indigo-500  opacity-90 hover:opacity-100 px-5 mr-3 rounded-xl text-white font-bold transition"
          tabIndex={0}
          type="button"
          style={{ justifyContent: "space-between", pointerEvents: "auto" }}
        >
          View Raffle
        </button>
      </div>

      {loading ? (
        <div className="snippet" data-title=".dot-spin">
          <div className="stage">
            <div className="dot-spin"></div>
          </div>
        </div>
      ) : (
        <div className="lg:grid-cols-3 xl:grid-cols-4 grid grid-cols-1 gap-6 sm:grid-cols-2 md:gap-8 px-8">
          {isSolanaNftData.map((nft, i) => (
            <div className="lazyload-wrapper " key={i}>
              <div className="card rounded-2xl overflow-hidden group md:hover:scale-[1.03] transition">
                <div className="relative">
                  <div className="absolute top-2 right-2 z-50 flex"></div>
                  <div className=" aspect-w-1 aspect-h-1 cursor-pointer">
                    <img
                      className="h-full object-center object-cover"
                      src={nft.imageUrl}
                      alt=""
                      loading="lazy"
                    />
                  </div>
                </div>
                <div className="p-4 bg-white dark:bg-offbase transition-all overflow-hidden rounded-b-2xl">
                  <div className="flex items-center">
                    <div className="line-clamp-1 font-bold text-gray-500 dark:text-purple-400/70 hover:text-lime-500 text-sm mr-1 capitalize">
                      {nft.collectionName}
                    </div>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 512 512"
                      className="w-4 fill-current text-lime-500"
                    >
                      <path d="M504 256c0 136.967-111.033 248-248 248S8 392.967 8 256 119.033 8 256 8s248 111.033 248 248zM227.314 387.314l184-184c6.248-6.248 6.248-16.379 0-22.627l-22.627-22.627c-6.248-6.249-16.379-6.249-22.628 0L216 308.118l-70.059-70.059c-6.248-6.248-16.379-6.248-22.628 0l-22.627 22.627c-6.248 6.248-6.248 16.379 0 22.627l104 104c6.249 6.249 16.379 6.249 22.628.001z" />
                    </svg>
                  </div>
                  <h2 className="text-left line-clamp-1 font-bold text-xl dark:text-white">
                    {nft.name}
                  </h2>

                  <div
                    onClick={() => sendDeatils(nft)}
                    className="from-indigo-600 to-indigo-500 border-indigo-500 dark:border-purple-500 dark:text-purple-500 dark:hover:to-purple-900/50 dark:hover:from-purple-900/50 block text-center py-3 mt-2 bg-gradient-to-t opacity-90 hover:opacity-100 text-white text-xl rounded-2xl border dark:to-transparent dark:from-transparent dark:border-2 transition-all  cursor-pointer"
                  >
                    <strong className="block leading-tight">Create Raffle</strong>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      )}
    </>
  );
}

export default Raffle;
